<template>
  <div class="com-container" @contextmenu.prevent="revertMap">
    <div class="com-chart" ref="map_ref"></div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      chartInstance: null,
      allData: null,
      mapData: {}, // 所获取的省份的地图矢量数据
      bChart: null,
      driving: null, //百度实例中的导航实例
      driveStatus: 0, //0就是还没有开始骑行，1就是开始骑行了
      startPoint: null,
      endPoint: null,
      distance: 0,
    };
  },
  mounted() {
    this.$notify({
      message: "点击位置开始骑行",
    });
    this.initChart();
    window.addEventListener("resize", this.screenAdapter);
    this.screenAdapter();
  },
  destroyed() {
    window.removeEventListener("resize", this.screenAdapter);
  },

  methods: {
    async initChart() {
      this.chartInstance = this.$echarts.init(this.$refs.map_ref);
      const initOption = {
        bmap: {
          center: [114.064747, 22.551052],
          zoom: 14,
          roam: true,

          mapStyle: {
            styleJson: [
              {
                featureType: "manmade",
                elementType: "all",
                stylers: {},
              },
            ],
          },
        },
      };
      this.chartInstance.setOption(initOption);
      // 获取百度的map实例
      this.bChart = this.chartInstance
        .getModel()
        .getComponent("bmap")
        .getBMap();
      //获取导航实例
      this.driving = new BMap.DrivingRoute(this.bChart, {
        renderOptions: {
          map: this.bChart,
          autoViewport: true,
        },
        onSearchComplete: (res) => {
          this.distance = res.getPlan(0).getDistance(true);
          console.log(this.endPoint);
          this.$http
            .post("/orderdetails/end", {
              endLatitude: this.endPoint.lat,
              endLongitude: this.endPoint.lng,
              distance: this.distance.slice(0, -2)*1000,
            })
            .then(
              (value) => {
                console.log(value);
                if (value.code === 0) {
                  this.$message.error("服务器故障，请重试");
                } else {
                  setTimeout(() => {
                    this.$message.success(
                      "骑行结束，本次骑行距离为：" + this.distance
                    );
                  }, 1000);
                }
              },
              (reason) => {
                this.$message.error("出现了未知异常");
              }
            );
        },
      });
      // 用百度实例获取点击事件
      this.bChart.addEventListener("click", (e) => {
        // var start = new BMap.Point(116.310791, 40.003419);
        // var end = new BMap.Point(116.486419, 39.877282);
        // driving.search(start, end);
        if (this.driveStatus === 0) {
          this.$confirm("是否开始骑行", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning",
          })
            .then(() => {
              this.$http
                .post("/orderdetails/start", {
                  startLatitude: e.point.lat,
                  startLongitude: e.point.lng,
                })
                .then(
                  (value) => {
                    if (value.code === 0) {
                      this.$message.error("服务器故障，请重试");
                    } else {
                      this.$message.success("骑行已开始");
                      this.startPoint = new BMap.Point(
                        e.point.lng,
                        e.point.lat
                      );
                      this.driveStatus = 1;
                    }
                  },
                  (reason) => {
                    this.$message.error("出现了未知异常");
                  }
                )
                .catch((err) => err);
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: "已取消",
              });
            });
        } else {
          this.$confirm("是否结束骑行", "提示", {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning",
          })
            .then(() => {
              this.endPoint = new BMap.Point(e.point.lng, e.point.lat);
              this.driving.search(this.startPoint, this.endPoint);
              this.driveStatus = 0;
            })
            .catch(() => {
              this.$message({
                type: "info",
                message: "已取消",
              });
            });
        }
      });
    },
    screenAdapter() {
      const titleFontSize = (this.$refs.map_ref.offsetWidth / 100) * 3.6;
      const adapterOption = {
        title: {
          textStyle: {
            fontSize: titleFontSize,
          },
        },
      };
      this.chartInstance.setOption(adapterOption);
      this.chartInstance.resize();
    },
  },
};
</script>
