<template>
  <div class="form-structor">
    <div class="signup">
      <h2 class="form-title" id="signup">西柚碳迹</h2>

      <div class="form-holder">
        <input type="text" class="input" v-model="phone" placeholder="手机号" />
        <input
          type="text"
          class="input"
          v-model="verNum"
          placeholder="验证码"
        />
        <div class="msg">
          |
          <span v-if="!sended" class="vertify" @click="vertify"
            >获取验证码</span
          >
          <span v-else class="time">{{ time }}s</span>
        </div>
      </div>
      <p class="warn">{{ warn }}</p>
      <button class="submit-btn" @click="login">登录</button>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      phone: "18781062970",
      verNum: "6348",
      time: 60,
      sended: false,
      warn: "",
      timer1: null,
    };
  },
  methods: {
    // 获取验证码
    async vertify() {
      if (this.phone === "") {
        this.warn = "请先输入手机号";
        return;
      }
      const regPhone = /^\d{15,17}$/;
      if (regPhone.test(this.phone)) {
        this.warn = "手机号格式错误";
        return;
      }
      this.warn = "";

      const { data: res } = await this.$http.post("/user/sendMsg", {
        phone: this.phone,
      }).catch(err=>{
        this.$message.error("出现了系统性的错误");
      })
      if (res.code === 0) {
        this.warn = "发送失败";
      } else {
        this.$message.success("验证码已发送");
        this.sended = true;
        //   开启计时
        this.timer1 = setInterval(() => {
          this.time--;
          if (this.time === 0) {
            clearInterval(this.timer1);
            this.sended = false;
            this.time = 60;
          }
        }, 1000);
      }
    },
    async login() {
      const { data: res } = await this.$http.post("/user/login", {
        phone: this.phone,
        code: this.verNum,
      });
      if (res.code === 0) {
        this.warn = "验证码错误";
      }
      if (res.code === 1) {
        this.$router.push("map");
      }
    },
  },
};
</script>

<style lang="less" scope>
html,
body {
  position: relative;
  min-height: 100vh;
  background-color: #e1e8ee;
  display: flex;
  align-items: center;
  justify-content: center;
}

.form-structor {
  background-color: #222;
  border-radius: 15px;
  height: 550px;
  width: 350px;
  position: relative;
  overflow: hidden;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);

  &::after {
    content: "";
    opacity: 0.8;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-repeat: no-repeat;
    background-position: left bottom;
    background-size: 500px;
    background-image: url("https://images.unsplash.com/photo-1503602642458-232111445657?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=bf884ad570b50659c5fa2dc2cfb20ecf&auto=format&fit=crop&w=1000&q=100");
  }

  .signup {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 65%;
    z-index: 5;
    .form-title {
      color: #fff;
      font-size: 1.7em;
      text-align: center;
    }

    .form-holder {
      border-radius: 15px;
      background-color: #fff;
      overflow: hidden;
      margin-top: 50px;
      opacity: 1;
      visibility: visible;

      .input {
        border: 0;
        outline: none;
        box-shadow: none;
        display: block;
        height: 30px;
        line-height: 30px;
        padding: 8px 15px;
        border-bottom: 1px solid #eee;
        width: 100%;
        font-size: 12px;

        &:last-child {
          border-bottom: 0;
        }
        &::-webkit-input-placeholder {
          color: rgba(0, 0, 0, 0.4);
        }
      }
      .msg {
        color: #999999;
        position: absolute;
        top: 143px;
        left: 143px;
        span:first-child {
          font-size: 12px;
        }
        .vertify {
          font-size: 12px;
          color: #b3b5b3;
          cursor: pointer;
        }
        .time {
          left: 20px;
          position: relative;
          color: #e99898;
        }
      }
    }
    .warn {
      height: 20px;
      color: #f83a3a;
      font-size: 13px;
      position: relative;
      left: 15px;
      top: 5px;
    }
    .submit-btn {
      background-color: rgba(0, 0, 0, 0.4);
      color: rgba(256, 256, 256, 0.7);
      border: 0;
      border-radius: 15px;
      display: block;
      margin: 22px auto;
      padding: 15px 45px;
      width: 100%;
      font-size: 13px;
      font-weight: bold;
      cursor: pointer;
      opacity: 1;
      visibility: visible;

      &:hover {
        background-color: rgba(0, 0, 0, 0.8);
      }
    }
  }
}
</style>